.body-height-exchange {
    height: calc(100vh - 110px);
  }
  .bot-exchange-title {
    text-align: center;
  }
  .bot-exchange-title .bot-exchange-title-txt {
    font-family: Trispace;
    font-style: normal;
    font-weight: 800;
    font-size: 90px;
    line-height: 108px;
    color: white;
  }
  .bot-exchange-title .bot-exchange-title-address {
    font-family: Trispace;
    font-style: normal;
    font-weight: 600 !important;
    font-size: 27px;
    line-height: 31px;
    margin-top: 25px;
    margin-bottom: 0;
    color: white;
    width: 800px;
  }
  .bot-exchange-title .first-sort-box {
    width: 1000px;
  }
  .bot-exchange-content {
    padding-top: 20px;
  }
  .bot-exchange-content .sort-box {
    padding-left: 100px;
  }
  .bot-exchange-content .sortings {
    margin-left: 60px;
  }
  .bot-exchange-content .card-img-bot {
    max-width: 250px;
  }
  .bot-exchange-content .card-bot {
    padding-top: 15px;
    text-align: center;
  }
  .bot-exchange-content .bot-box {
    margin-top: 30px;
  }
  .bot-exchange-content .card-text-bot {
    font-family: Trispace;
    font-style: normal;
    font-weight: 600 !important;
    font-size: 27px;
    line-height: 31px;
  }
@media only screen and (max-width: 420px) {
    .body-height-exchange {
        height: calc(100vh - 80px);
      }
}