.body-height-mybot {
  min-height: calc(100vh - 350px);
}
.my-bot-content {
  padding-top: 50px;
}
.my-bot-content .sort-box {
  padding-left: 100px;
}
.my-bot-content .sortings {
  margin-left: 60px;
}
.my-bot-content .card-img-bot {
  width: 250px;
}
.my-bot-content .card-bot {
  padding-top: 15px;
  text-align: center;
}
.my-bot-content .bot-box {
  margin-top: 30px;
}
.my-bot-content .card-text-bot {
  font-family: Trispace;
  font-style: normal;
  font-weight: 600 !important;
  font-size: 27px;
  line-height: 31px;
}

.my-bot-title {
  text-align: center;
}
.my-bot-title .my-bot-title-txt {
  font-family: Trispace;
  font-style: normal;
  font-weight: 800;
  font-size: 96px;
  line-height: 108px;
  color: white;
}
.my-bot-title {
  font-family: Trispace;
  font-style: normal;
  font-weight: 600 !important;
  font-size: 27px;
  line-height: 31px;
  margin-top: 40px;
  margin-bottom: 0;
  color: white;
}
.my-bot-title-address {
  font-family: Trispace;
  font-style: normal;
  font-weight: 600 !important;
  font-size: 27px;
  line-height: 31px;
  margin-top: 40px;
  margin-bottom: 0;
  color: white;
}
.card-bot {
  width: 18rem;
}
.cronos-bot-big-img {
  max-width: 350px;
  display: block;
}
@media only screen and (max-width: 350px) {
  .my-bot-title-address {
    font-size: 9px !important;
  }
  .card-bot {
    width: 15rem !important;
  }
  .card-img-bot {
    width: 200px !important;
  }
}
@media only screen and (min-width: 351px) and (max-width: 470px) {
  .my-bot-title-address {
    font-size: 12px !important;
  }
}
@media only screen and (max-width: 470px) {
  .cronos-bot-big-img {
    max-width: 230px;
    display: block;
  }
  .card-modal {
    width: 100% !important;
  }
}
@media only screen and (max-width: 525px) {
  .body-height-mybot {
    height: auto;
  }
}
@media only screen and (min-width: 471px) and (max-width: 580px) {
  .my-bot-title-address {
    font-size: 16px !important;
  }
}
@media only screen and (max-width: 810px) {
  .my-bot-title-address {
    font-size: 20px;
  }
}
